
html,
body,
#app,
.view {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
.view {
  width: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.header {
  margin: 50px 5%;
}
.footer {
  position: relative;
  bottom: -60px;
  padding-bottom: 10px;
  text-align: center;
}
.footer a {
  color: #a0aabf;
  text-decoration: none;
  cursor: pointer;
}
.footer a:hover {
  text-decoration: underline;
}


