
textarea,
.preview {
  height: 100%;
  overflow-y: scroll;
  padding: 20px;
  width: calc(50% - 46px);
  vertical-align: top;
  border: 1px solid #e1e1e1;
}
textarea {
  border: none;
  border-right: 1px solid #ccc;
  resize: none;
  outline: none;
  background-color: #f6f6f6;
  font-size: 14px;
  font-family: 'Monaco', courier, monospace;
}
.preview {
  display: inline-block;
  overflow-x: hidden;
  word-wrap: break-word;
  word-break: normal;
}
#gpk-editor {
  padding: 0 5%;
  height: 100%;
}
#gpk-editor p {
  line-height: 27px;
}
#gpk-editor pre {
  border-color: #d3daea;
  display: block;
  word-break: break-all;
  word-wrap: break-word;
  color: #333;
  background-color: #f5f7fa;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 30px;
  padding: 0 10px;
}
#gpk-editor pre code {
  color: #f66;
  padding: 0;
  font-size: inherit;
  color: inherit;
  white-space: pre-wrap;
  background-color: transparent;
  border-radius: 0;
  line-height: 30px;
}
#gpk-editor blockquote {
  color: #666;
  margin: 0;
  padding-left: 3em;
  border-left: 0.5em #eee solid;
}
#gpk-editor blockquote p {
  margin: 0px;
  line-height: 26px;
}
#gpk-editor ul li {
  line-height: 30px;
}
#gpk-editor hr {
  box-sizing: content-box;
  height: 0;
}
#gpk-editor table,
#gpk-editor table>thead>tr>th,
#gpk-editor table>thead>tr>td,
#gpk-editor table>tbody>tr>th,
#gpk-editor table>tbody>tr>td,
#gpk-editor table>tfoot>tr>th,
#gpk-editor table>tfoot>tr>td {
  border: 1px solid #ddd;
  padding: 10px;
}
#gpk-editor table {
  border-collapse: collapse;
}
#gpk-editor a {
  color: #a0aabf;
  text-decoration: underline;
}
#gpk-editor code {
  color: #c7254e;
  background-color: #f9f2f4;
  border-radius: 4px;
}


